/* vdl-color using default palette 'vdl-palette-1'
  Examples of use:
  vdl-color(15);

  to specify a different color palette:
  vdl-color(4, $some-other-palette);
 */
.popover {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1060;
  display: block;
  max-width: 276px;
  padding: 1px;
  font-style: normal;
  font-weight: normal;
  letter-spacing: normal;
  line-break: auto;
  line-height: 0.938;
  text-decoration: none;
  text-shadow: none;
  text-transform: none;
  white-space: normal;
  word-break: normal;
  word-spacing: normal;
  word-wrap: break-word;
  -webkit-background-clip: padding-box;
  background-clip: padding-box;
  border: 1px solid #e5e5e5;
  font-family: Roboto Light, sans-serif;
  font-size: 0.813rem;
  outline-offset: 2px;
  outline: -2px;
  background-color: #f9f9f9;
  border-radius: 3px;
  width: 100%; }
  .popover::before, .popover::after {
    position: absolute;
    display: block;
    width: 0;
    height: 0;
    border-color: transparent;
    border-style: solid; }
  .popover::before {
    content: '';
    border-width: 11px; }
  .popover::after {
    content: '';
    border-width: 10px; }
  .popover.popover-top {
    margin-top: -15px;
    padding: 15px 15px 0;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.15); }
    .popover.popover-top::before, .popover.popover-top::after {
      left: 50%;
      border-bottom-width: 0; }
    .popover.popover-top::before {
      bottom: -11px;
      margin-left: -11px;
      border-top-color: #f9f9f9; }
    .popover.popover-top::after {
      bottom: -15px;
      margin-left: -11px;
      border-top-color: #f9f9f9;
      box-sizing: border-box;
      border: 8px solid transparent;
      border-color: transparent transparent #f9f9f9 #f9f9f9;
      transform-origin: 0 0;
      transform: rotate(-45deg);
      box-shadow: -2px 2px 2px 0 rgba(0, 0, 0, 0.15); }
  .popover.popover-right {
    margin-left: 20px;
    padding: 15px 15px 0;
    box-shadow: -2px 0 2px 0 rgba(0, 0, 0, 0.15); }
    .popover.popover-right::before, .popover.popover-right::after {
      top: 50%;
      border-left-width: 0; }
    .popover.popover-right::before {
      left: -11px;
      margin-top: -11px;
      border-right-color: #f9f9f9; }
    .popover.popover-right::after {
      margin-top: -11px;
      left: 0;
      border-right-color: #f9f9f9;
      box-sizing: border-box;
      border: 8px solid transparent;
      border-color: transparent transparent #f9f9f9 #f9f9f9;
      transform-origin: 0 0;
      transform: rotate(45deg);
      box-shadow: -2px 2px 2px 0 rgba(0, 0, 0, 0.15); }
  .popover.popover-bottom {
    margin-top: 15px;
    padding: 15px 15px 0;
    box-shadow: 0 -2px 2px 0 rgba(0, 0, 0, 0.15); }
    .popover.popover-bottom::before, .popover.popover-bottom::after {
      left: 50%;
      border-top-width: 0; }
    .popover.popover-bottom::before {
      top: -11px;
      margin-left: -11px;
      border-bottom-color: #f9f9f9; }
    .popover.popover-bottom::after {
      top: 0;
      margin-left: 11px;
      border-bottom-color: #f9f9f9;
      box-sizing: border-box;
      border: 8px solid transparent;
      border-color: transparent transparent #f9f9f9 #f9f9f9;
      transform-origin: 0 0;
      transform: rotate(135deg);
      box-shadow: -2px 2px 2px 0 rgba(0, 0, 0, 0.15); }
    .popover.popover-bottom .popover-title::before {
      position: absolute;
      top: 0;
      left: 50%;
      display: block;
      width: 20px;
      margin-left: -10px;
      content: '';
      border-bottom: 1px solid #f7f7f7; }
  .popover.popover-left {
    margin-left: -15px;
    padding: 15px 15px 0;
    box-shadow: 2px 0 2px 0 rgba(0, 0, 0, 0.15); }
    .popover.popover-left::before, .popover.popover-left::after {
      top: 50%;
      border-right-width: 0; }
    .popover.popover-left::before {
      right: -11px;
      margin-top: -11px;
      border-left-color: #f9f9f9; }
    .popover.popover-left::after {
      right: -15px;
      margin-top: 11px;
      border-left-color: #f9f9f9;
      box-sizing: border-box;
      border: 8px solid transparent;
      border-color: transparent transparent #f9f9f9 #f9f9f9;
      transform-origin: 0 0;
      transform: rotate(225deg);
      box-shadow: -2px 2px 2px 0 rgba(0, 0, 0, 0.15); }

.popover-title {
  padding-bottom: 9px;
  margin-top: 0;
  border: solid 1px transparent;
  border-bottom-color: #e5e5e5;
  font-family: Roboto, sans-serif;
  font-size: 0.938rem;
  line-height: 1rem;
  background-color: #f9f9f9;
  color: #414142;
  padding: 0 0 9px;
  margin-bottom: 9px; }

.popover-content {
  padding: 0 0 15px;
  font-size: 0.813rem;
  line-height: 1rem; }

.vdl-popover-button-container {
  padding-top: 15px;
  padding-left: 0;
  padding-right: 0;
  display: flex;
  flex-wrap: nowrap; }
  .vdl-popover-button-container button {
    flex-grow: 1; }
  .vdl-popover-button-container button:not(:first-child) {
    margin-left: 30px; }
